<template>
  <div id="home-div">
    <el-container class="home-con">
      <el-header class="header-con">
        <el-row :gutter="0">
          <el-col :xs="0" :sm="10" :md="10" :lg="10" :xl="10"
            ><div class="home-title">凌云博客</div></el-col
          >
          <el-col :xs="0" :sm="1" :md="4" :lg="6" :xl="8"
            ><div style="height: 60px"></div
          ></el-col>
          <el-col :xs="24" :sm="13" :md="10" :lg="8" :xl="6">
            <el-menu
              :default-active="activeIndex"
              class="el-menu-demo"
              mode="horizontal"
              background-color="#FFFFFF"
              text-color="#343A40"
              active-text-color="#1C60F3"
              router
            >
              <el-menu-item index="/main">首页</el-menu-item>
              <el-menu-item index="/message">留言</el-menu-item>
              <el-menu-item index="/login" v-if="!$store.getters.getToken.token"
                >登录/注册</el-menu-item
              >
              <el-submenu index="/user" v-if="$store.getters.getToken.token">
                <template slot="title">我的账户</template>
                <el-menu-item index="/user/userInfo">个人资料</el-menu-item>
                <el-menu-item index="/login">退出登录</el-menu-item>
              </el-submenu>
            </el-menu>
          </el-col>
        </el-row>
      </el-header>
      <el-main class="main-con">
        <div class="testdiv">
          <el-card class="box-card_title">
            <div class="welcome_div">
              <div class="text item">留<span>言</span>板</div>
              <div class="welcome">
                欢迎您的来访，有任何意见或者建议，可以在此提出来
              </div>
            </div>
          </el-card>

          <el-card class="box-card-main-div">
            <div>
              <div style="margin: 20px 0"></div>
              <div class="input_div">
                <el-popover
                  placement="top"
                  title="表情"
                  width="500"
                  height="700"
                  trigger="click"
                  v-model="emojiShow"
                >
                  <el-button slot="reference" style="margin-bottom: 3px"
                    >😀表情~</el-button
                  >
                  <div class="browBox">
                    <ul>
                      <li
                        v-for="(item, index) in faceList"
                        :key="index"
                        @click="getBrow(index)"
                      >
                        {{ item }}
                      </li>
                    </ul>
                  </div>
                </el-popover>
                <el-input
                  id="input-id"
                  @focus="gotoLogin"
                  :rows="5"
                  style="margin-bottom: 6px"
                  type="textarea"
                  placeholder="留下你的足迹吧！！"
                  @keyup.enter.native="submitMessage"
                  v-model="content"
                  maxlength="200"
                  show-word-limit
                >
                </el-input>
                <div class="cue-words">Ctrl+Enter 发送</div>

                <el-button
                  style="background-color: #e9b5fa; border: 0"
                  class="submit-btn"
                  type="primary"
                  @click="submitMessage"
                  :disabled="content == ''"
                  icon="el-icon-s-promotion"
                  >发送
                </el-button>
              </div>
            </div>

            <div>
              <div class="box-card cue-words-content-card" shadow="hover">
                <div
                  class="cue-words-content-div-for"
                  v-for="message in commentList"
                  :key="message.id"
                >
                  <img
                    v-if="message.headimg"
                    class="cue-words-content-avatar cue-words-content-avatar-img"
                    :src="message.headimg"
                  />
                  <el-avatar
                    v-if="!message.headimg"
                    class="cue-words-content-avatar"
                    shape="square"
                    size="medium"
                    >{{ message.username.charAt(0) }}</el-avatar
                  >
                  <div class="cue-words-content-div">
                    <div class="cue-words-content-name">
                      {{ message.username }}
                    </div>
                    <div class="cue-words-content-time">
                      {{ message.createtime }}
                      <el-button
                        class="reply-button"
                        icon="el-icon-s-promotion"
                        type="text"
                        @click="openReply(message)"
                        >回复</el-button
                      >
                    </div>
                  </div>
                  <div class="cue-words-content-text">
                    <div>
                      {{ message.content }}
                    </div>
                  </div>
                  <div v-if="message.reply">
                    <div style="margin: 20px 0"></div>
                    <div class="input_div-reply">
                      <el-popover
                        placement="top"
                        title="表情"
                        width="500"
                        height="700"
                        trigger="click"
                        v-model="emojiShowtwo"
                      >
                        <el-button slot="reference" style="margin-bottom: 3px"
                          >😀表情~</el-button
                        >
                        <div class="browBox">
                          <ul>
                            <li
                              v-for="(item, index) in faceList"
                              :key="index"
                              @click="getBrowtwo(index)"
                            >
                              {{ item }}
                            </li>
                          </ul>
                        </div>
                      </el-popover>
                      <el-input
                        id="input-id"
                        @focus="gotoLogin"
                        :rows="5"
                        style="margin-bottom: 6px"
                        type="textarea"
                        placeholder="留下你的足迹吧！！"
                        @keyup.enter.native="submitMessage"
                        v-model="contenttwo"
                        maxlength="200"
                        show-word-limit
                      >
                      </el-input>
                      <div class="cue-words">Ctrl+Enter 发送</div>

                      <el-button
                        style="background-color: #e9b5fa; border: 0"
                        class="submit-btn"
                        type="primary"
                        @click="submitReplyMessage(message.id)"
                        :disabled="contenttwo == ''"
                        icon="el-icon-s-promotion"
                        >发送
                      </el-button>
                    </div>
                  </div>
                  <div
                    class="cue-words-content-reply"
                    v-for="commentreply in message.replyList"
                    :key="commentreply.id"
                  >
                    <img
                      v-if="commentreply.headimg"
                      class="cue-words-content-avatar cue-words-content-avatar-img"
                      :src="commentreply.headimg"
                    />
                    <el-avatar
                      v-if="!commentreply.headimg"
                      class="cue-words-content-avatar"
                      shape="square"
                      size="medium"
                      >{{ commentreply.username.charAt(0) }}</el-avatar
                    >
                    <div class="cue-words-content-div">
                      <div class="cue-words-content-name">
                        {{ commentreply.username }}
                        <span style="color: #1c60f3">@</span
                        >{{ commentreply.tousername }}
                      </div>
                      <div class="cue-words-content-time">
                        {{ commentreply.createtime }}
                        <el-button
                          class="reply-button"
                          icon="el-icon-s-promotion"
                          type="text"
                          @click="openReply(commentreply)"
                          >回复</el-button
                        >
                      </div>
                    </div>
                    <div class="cue-words-content-text">
                      <div>
                        {{ commentreply.content }}
                      </div>
                    </div>
                    <div v-if="commentreply.reply">
                      <div style="margin: 20px 0"></div>
                      <div class="input_div-reply">
                        <el-popover
                          placement="top"
                          title="表情"
                          width="500"
                          height="700"
                          trigger="click"
                          v-model="emojiShowtwo"
                        >
                          <el-button slot="reference" style="margin-bottom: 3px"
                            >😀表情~</el-button
                          >
                          <div class="browBox">
                            <ul>
                              <li
                                v-for="(item, index) in faceList"
                                :key="index"
                                @click="getBrowtwo(index)"
                              >
                                {{ item }}
                              </li>
                            </ul>
                          </div>
                        </el-popover>
                        <el-input
                          id="input-id"
                          @focus="gotoLogin"
                          :rows="5"
                          style="margin-bottom: 6px"
                          type="textarea"
                          placeholder="留下你的足迹吧！！"
                          @keyup.enter.native="submitMessage"
                          v-model="contenttwo"
                          maxlength="200"
                          show-word-limit
                        >
                        </el-input>
                        <div class="cue-words">Ctrl+Enter 发送</div>

                        <el-button
                          style="background-color: #e9b5fa; border: 0"
                          class="submit-btn"
                          type="primary"
                          @click="submitReplyMessage(commentreply.id)"
                          :disabled="contenttwo == ''"
                          icon="el-icon-s-promotion"
                          >发送
                        </el-button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </el-card>
        </div>
        <div class="bottom_div">
          <div class="bottom_text">
            <div class="span_text_div">
              <span class="span_text">凌云博客</span>
              <el-divider direction="vertical"></el-divider>
              <span class="span_text">关于本站</span>
            </div>

            <div class="span_text_div">
              <span class="span_text">© 2023 - Ye Lingfa's blog</span>
            </div>
            <div class="span_text_div">
              <span class="span_text" @click="gotoBeiAn"
                >闽ICP备2022015900号</span
              >
            </div>
          </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
const appData = require("@/assets/img/emojis.json");
export default {
  data() {
    return {
      content: "",
      contenttwo: "",
      emojiShow: false,
      emojiShowtwo: false,
      faceList: [],
      getBrowString: "",
      activeIndex: "/message",
      pageInfo: {
        pageSize: 10,
        pageNum: 1,
      },
      commentList: [],
    };
  },
  created() {
    this.loadEmojis();
  },
  methods: {
    openReply(message) {
      const token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$confirm("请先登录！登录后才能评论~", "提示", {
          confirmButtonText: "去登录",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.$router.push("/login");
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "您真不打算登录啊~",
            });
          });
      } else {
        this.content = "";
        this.contenttwo = "";
        if (message.reply == true) {
          this.commentList.forEach((comment) => {
            comment.reply = false;
            if (comment.replyList) {
              comment.replyList.forEach((reply) => {
                reply.reply = false;
              });
            }
          });
          return;
        }
        this.commentList.forEach((comment) => {
          comment.reply = false;
          if (comment.replyList) {
            comment.replyList.forEach((reply) => {
              reply.reply = false;
            });
          }
        });
        message.reply = true;
      }

      // for(var i = 0;i<this.commentList.length;i++){
      //     if(this.commentList[i] === message){
      //         this.commentList[i].reply = !message.reply
      //         alert(this.commentList[i].reply)
      //     }
      // }
      // for(var j = 0;j<this.commentList.length;j++){
      //     if(this.commentList[i].replyList === message){
      //         this.commentList[i].replyList.reply = !message.reply
      //     }
      // }
    },

    loadEmojis() {
      for (let i in appData) {
        this.faceList.push(appData[i].char);
      }
    },
    getBrow(index) {
      for (let i in this.faceList) {
        if (index == i) {
          this.getBrowString = this.faceList[index];
          this.content += this.getBrowString;
        }
      }
      this.emojiShow = false;
    },
    getBrowtwo(index) {
      for (let i in this.faceList) {
        if (index == i) {
          this.getBrowString = this.faceList[index];
          this.contenttwo += this.getBrowString;
        }
      }
      this.emojiShowtwo = false;
    },
    submitReplyMessage(id) {
      console.log(id);
      this.$api.comment
        .addMessage({ content: this.contenttwo, paterid: id })
        .then((res) => {
          if (res.data.code === 200) {
            this.getCommentPage();
            this.$message.success("留言成功！");
            this.content = "";
            this.contenttwo = "";
          } else {
            console.log(res.data);
            this.$message.error("留言失败！");
          }
        });
    },
    submitMessage() {
      const token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$confirm("请先登录！登录后才能评论~", "提示", {
          confirmButtonText: "去登录",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.$router.push("/login");
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "您真不打算登录啊~",
            });
          });
      }
      this.$api.comment.addMessage({ content: this.content }).then((res) => {
        if (res.data.code === 200) {
          this.getCommentPage();
          this.$message.success("留言成功！");
          this.content = "";
          this.contenttwo = "";
        } else {
          console.log(res.data);
          this.$message.error("留言失败！");
        }
      });
    },
    gotoBeiAn() {
      window.location.href = "https://beian.miit.gov.cn/";
    },
    async getCommentPage() {
      const res = await this.$api.comment.commentPage(this.pageInfo);
      if (res.data.code === 200) {
        this.commentList = res.data.data;
      } else {
        this.$message.error("请求失败！请稍后再试");
      }
    },
    gotoLogin(e) {
      const token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$confirm("请先登录！登录后才能评论~", "提示", {
          confirmButtonText: "去登录",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.$router.push("/login");
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "您真不打算登录啊~",
            });
          });
        e.srcElement.blur();
      }
    },
  },

  mounted() {
    this.getCommentPage();
    this.$store.dispatch("asyncUpdatedActiveIndex","/message")
  },
};
</script>

<style scoped>
