  padding: 12px 25px;
  background-color: #f5f5f7;
  border-radius: 5px;
  float: left;
  margin-left: 40px;
  clear: both;
}
.cue-words-content-reply {
  float: left;
  margin-left: 40px;
  width: 96%;
  clear: both;
}
.cue-words-content-avatar {
  float: left;
  background-color: #e9b5fa;
  font-size: 19px;
  clear: both;
}
.cue-words-content-div {
  float: left;
  margin-left: 10px;
  margin-top: -2px;
}
.cue-words-content-card {
  width: 90%;
  margin: auto;
  margin-top: 90px;
}
.cue-words {
  color: #c2c7ce;
  float: left;
}
.input_div {
